<template>
  <div class="tpl">
    <raw-loader :mytxt=" require('!raw-loader!./TabulatorVueCom.vue').default "></raw-loader>
    <h1>This is Tabulator First [ {{$route.params.id}} ]</h1>
    <div>
      <div class="mb-1">
      </div>
      <com-tabulator></com-tabulator>
      this is test
      
    </div>
  </div>
</template>
<script>
import {TabulatorFull as Tabulator} from 'tabulator-tables';
import Vue from 'vue';
import HelloWorld from '@/components/HelloWorld.vue'
import ComTabulator from '@/components/ComTabulator.vue'


Vue.component('example-component', {
  data: function () {
    return {
      tabulator: null, //variable to hold your table
      tableData: [], //data for table to display
    }
  },
  mounted(){
    //instantiate Tabulator when element is mounted
    this.tabulator = new Tabulator(this.$refs.table, {
      data: this.tableData, //link data to table
      reactiveData:true, //enable data reactivity
      columns: [], //define table columns
    });
  },
  template: `<div ref="table"></div>hello world` //create table holder element
});
Vue.component('test',{
  template:`<div>hello world</div>`
});

export default {
  name: 'Tpl',
  components: {
    HelloWorld,
    ComTabulator,
  },
  data(){
    return {
      msg:"thuc 101"
    }
  },
  mounted(){
  },
  methods:{
  }
}
</script>
<style scoped>
  @import '~tabulator-tables/dist/css/tabulator.css';  
  .tabulator {
    font-size: 10px;
    border: none;
  }

  .tabulator .tabulator-header {
    font-weight: normal;
    border-bottom: 1px solid;
    border-bottom-color: rgba(0, 0, 0, 0.12);
    color: rgba(0, 0, 0, 0.54);
  }

  .tabulator .tabulator-header .tabulator-col {
    height: 48px;
    background-color: #ffffff;
    border: none;
    padding: 0 7px;
  }

  .tabulator-row {
    border-bottom: 1px solid;
    border-bottom-color: rgba(0, 0, 0, 0.12);
    min-height: 48px;
    vertical-align: middle;
  }

  .tabulator-row .tabulator-cell {
    padding: 16px 7px;
    border-right: none;
    vertical-align: middle;
  }

  .tabulator-row .tabulator-cell:first-of-type,
  .tabulator .tabulator-header .tabulator-col:first-of-type {
    padding-left: 24px;
  }

  .tabulator-row.tabulator-row-even {
    background-color: #ffffff;
  }



</style>

